@use "variables" as v;
@use 'base/mixins' as m;

h1,
h2,
h3 {
  font-family: v.$fuente_principal;
  margin: 0 0 calc(v.$separacion / 2) 0;
  font-weight: 900;
  color: v.$rojo;
  text-align: center;
}

h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
}
h3 {
  font-size: 3rem;
  text-align: center;
}
p {
  color: v.$negro;
  font-size: 2rem;
}

[class$="__heading"] {
  @include m.nombre-pagina;
}
