/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #F05454;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

[class$=__heading] {
  font-family: "Poppins", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.servicios {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.servicios__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    gap: 10rem;
  }
}
.servicios__heading {
  margin: 5rem 0;
  text-align: center;
  color: #F05454;
}

.servicio__titulo {
  color: #30475E;
  text-align: center;
}
.servicio__precio {
  font-size: 2.8rem;
  font-weight: 700;
  color: #F05454;
  text-align: center;
}
.servicio__iconos {
  display: flex;
  gap: 2rem;
  justify-content: center;
}

.horario {
  background-image: linear-gradient(to bottom, #F05454 0%, #F05454 50%, transparent 50%), url(../img/horarios.jpg);
  background-repeat: no-repeat;
  background-position: left top;
  background-size: cover;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .horario {
    background-image: url(../img/horarios.jpg);
    color: #FFFFFF;
    background-position: center, top left;
  }
}
.horario__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .horario__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.horario__contenido {
  grid-column-start: 2;
  grid-column-end: 3;
  background-color: #F05454;
  padding: 5rem;
}
.horario__heading {
  text-align: center;
  margin: 2rem 0;
}
.horario__texto {
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}

.tabla {
  width: 100%;
  border: 0.5rem solid #30475E;
  text-align: center;
  border-collapse: collapse;
}
.tabla__thead {
  background-color: #30475E;
}
.tabla__th {
  padding: 2rem;
}
.tabla__tr:hover {
  background-color: #ee3d3d;
}
.tabla__td {
  padding: 1rem;
  font-size: 2.2rem;
}

.ubicacion__heading {
  margin: 5rem 0;
  text-align: center;
  color: #F05454;
}

.mapa {
  height: 45rem;
  margin-top: 5rem;
}
.mapa__heading {
  margin: 0;
  text-align: left;
  font-size: 2.4rem;
}
.mapa__texto {
  font-size: 1.6rem;
}

.header {
  background-image: url(../img/header2.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 0;
  min-height: 30rem;
  position: relative;
}
@media (min-width: 768px) {
  .header {
    min-height: 40rem;
  }
}
.header::before {
  content: "";
  background-color: #1a1b15;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.6;
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
  position: relative;
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__titulo {
  font-size: 4.4rem;
  text-align: center;
  color: #F05454;
  font-weight: 700;
}
@media (min-width: 768px) {
  .header__titulo {
    font-size: 5rem;
  }
}
.header__texto {
  text-align: center;
  padding: 2rem;
}
.header__texto h1 {
  color: #FFFFFF;
}

.navegacion {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    margin-top: 0;
  }
}
.navegacion__enlace {
  color: #F05454;
  font-size: 2rem;
  font-weight: 700;
  background-color: #30475E;
  padding: 1rem;
  border-radius: 0.5rem;
  transition: all 0.2s ease-in;
}
.navegacion__enlace:hover {
  padding: 1.3rem;
}

.footer {
  font-size: 2rem;
  text-align: center;
  padding: 3rem 0;
}
.footer__copyright {
  color: #30475E;
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: 700;
}
.alerta__error {
  background-color: white;
  border-left: 0.5rem solid #F05454;
  color: #F05454;
}
.alerta__exito {
  background-color: #abfeab;
  border-left: 0.5rem solid #02db02;
  color: #017601;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: rgba(26, 27, 21, 0.8);
  font-size: 2.2rem;
  margin-bottom: 2.2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__select {
  padding: 1rem;
  border: 1px solid #aeaeae;
  border-radius: 0.5rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__submit {
  background-color: #30475E;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  width: auto;
  align-self: flex-end;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #1f2e3c;
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.acciones, .acciones--centrar {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}